@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";
@import "slick-theme";
@import "slick";

$textos: 'Open Sans', serif;
$light-color: #fff;
$accent-color: #65e496;
$text : 'Roboto Slab', serif ;
$bg-color: #f5f6fa;
$dark-color: #44444f;

body{
    font-family: $text;   
    box-sizing: border-box; 
}
.page-main{
    background-image: url('../img/bg-primary.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 160px;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1170px;
    margin: 0 auto;
    padding-top: 38px;
    padding-bottom: 166px;
    @media (max-width: 920px){
        flex-direction: column;
        padding-bottom: 136px;
    }
    @media (max-width:700px){
        padding-bottom: 60px;
    }
    &__logo{
        display: block;
        width: 137px;
        height: 28px;
        background-image: url('../img/svg/Logo.svg');
        @media (max-width: 920px){
            margin-bottom: 10px;
        }
    }
    &-list{
        list-style-type: none;
        display: flex;
        justify-content: flex-end;
        font-family: 'Open Sans', serif;
        @media (max-width:700px){
            display: none;
        }
        &__item{
            margin-left: 15px;
            padding-bottom: 5px;
            border-bottom: 3px solid transparent;
            transition: 0.3s;
            &:hover{
                border-bottom: 3px solid $accent-color;
            }
        }
        &__item.active{
            border-bottom: 3px solid $accent-color;
        }
        &__link{
            color: $light-color;
            text-decoration: none;
            letter-spacing: 0.5px;
        }
    }
}
.wrap{
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;    
    &--light{
        color: $light-color;
    }
}
.block-book{
    display: flex;
    justify-content: space-between;
    
    &-demo{
        display: block;
        position: relative;
        width: 447px;
        height: 643px;
        background-image: url(../img/svg/book.svg);
        background-repeat: no-repeat;
        flex-basis: 447px;
        @media (max-width: 768px){
            width: 347px;
            height: 443px;
            flex-basis: 347px;
        }
        @media (max-width:700px){
            display: none;
        }
        &__title{
            position: absolute;
            top: 130px;
            left: 130px;
            font-size: 36px;
            text-transform: uppercase;            
            letter-spacing: 1.4px;
            @media (max-width: 768px){
                top: 100px;
                left: 100px;
                font-size: 24px;
            }
            span{
                display: block;
                text-transform: none;
                font-family: textos;
                font-size: 14px;
                color: $accent-color;
                letter-spacing: 0.7px;
                margin-top: 20px;
                @media (max-width: 768px){
                    font-size: 12px;
                }
            }
        }
        &__author{
            position: absolute;
            bottom: 60px;
            left: 85px;
            font-family: textos;
            font-size: 14px;
            letter-spacing: 0.7px;
            color: $accent-color;
            @media (max-width: 768px){
                font-size: 11px;
            }
        }
    }
    
}

.block-text {
    flex-basis: calc(93% - 500px);
    @media (max-width: 920px){
        flex-basis: calc(93% - 420px);
    } 
    @media (max-width: 768px) {
        flex-basis:  calc(93% - 350px);
    }  
    @media (max-width: 700px){
       flex-basis: 100%;
    }          
    &__name{
        font-family: 'Open Sans', serif;
        font-size: 18px;
        letter-spacing: 0.7px;
        margin-bottom: 30px;
        @media (max-width: 768px){
            font-size: 12px;
            margin-bottom: 20px;
        }
        @media (max-width: 700px){
            font-size: 14px;
        }
        &-top{
            margin-top: 16px;
            margin-left: 0;
        }
    }
    &__title{
        font-size: 48px;
        margin-bottom: 40px;
        font-weight: 700; 
        letter-spacing: 1.8px; 
        @media (max-width: 768px){
            font-size: 28px;
        }
        @media (max-width: 700px){
            font-size: 30px;
        }
        &_bukee{
            text-transform: uppercase;
        }          
        span {
            display: block;
            margin-top: 9px;
            color: $accent-color;
            font-size: 18px;
            letter-spacing: 0.9px;
            @media (max-width: 768px){
                font-size: 12px;
            }
            @media (max-width: 700px){
                font-size: 14px;
            }
        }
    }
    &__descr{
        font-family: 'Open Sans', serif;
        font-size: 15px; 
        letter-spacing: 0.7px;
        line-height: 20px;
        margin-bottom: 24px;
        @media (max-width: 768px){
            font-size: 11px;
        }
    }
    &__btn{
        position: relative;
        display: block;
        background: linear-gradient(to top left, #65e49c, #65e4b0);
        color: $light-color;
        text-decoration: none;
        max-width: 250px;
        border-radius: 3px;
        text-align: center;
        margin-top: 48px;
        padding: 14px 0;
        letter-spacing: 0.7px;
        margin-bottom: 19px;
        box-shadow: 0 -2px #b2f1be inset;
        &--primary{
            box-shadow: 0 -2px #59ba88 inset;
        }
    }
    small{
        font-size: 12px;
        font-family: 'Open Sans', serif;
        font-style: italic;
    }
}
.book-author{
    padding-top: 161px;
    @media (max-width: 768px){
        padding-top: 120px;
    }
    
}
.block-author{
    display: flex;
    justify-content: space-between;
    @media (max-width: 700px){
        flex-direction: column;
    }
}
.block-ava{
    position: relative;
    &__svg{
        position: absolute;
        right: 0;
        z-index: -1;
    @media (max-width: 520px){
        width: 230px;
        height: 250px;
        left: 0;
    }    
    }
    &__img{
        margin-top: 47px;
        margin-right: 44px;
    @media (max-width: 520px){
        width: 230px;
        margin-top: 27px;
        margin-left: 24px;
    }     
    }
}
.block-counter{
    display: flex;
    justify-content: space-around;
    margin-top: 152px;
    margin-bottom: 160px;
    @media (max-width:700px){
        margin-top: 80px;
        margin-bottom: 80px;
        flex-direction: column;
    }
    p{
        font-family: 'Open Sans', serif;
        text-align: center;        
        span{
            font-family: $text;
            display: block;
            font-size: 80px;
            font-weight: 100;
            color: $accent-color;
            margin-bottom: 12px;
        }
    }
}
.page-book{
    background-color: $bg-color;
    padding-top: 160px;
    padding-bottom: 250px;
    @media (max-width: 840px){
        padding-top: 120px;
        padding-bottom: 120px;
    }
}
.book-contents{    
    &_text{
        text-align: center;        
    }
    &__wrap{
        margin-top: 75px;
        display: flex;
        justify-content: space-between;
        @media (max-width: 840px){
            flex-direction: column;
        }
    }
    &-about{
        flex-basis: 40%;
        @media (max-width: 840px){
            flex-basis: 100%;
        }
    }
    &-table{
        flex-basis: 50%;
        @media (max-width: 840px){
            flex-basis: 100%;
            margin-top: 50px;
        }
    }
    &__title{
        font-size: 24px;
        letter-spacing: 0.9px;
        line-height: 24px;
        margin-bottom: 30px;
    }
    &__text{
        font-family: textos;
        font-size: 15px;
        letter-spacing: 0.9px;
        line-height: 24px;
        font-weight: 400;
        margin-bottom: 40px;
    }
    &__featured{
        display: flex;
        span{
            font-weight: 700;
            font-size: 18px;
            line-height: 24px;
            letter-spacing: 0.7px;
        }
        ul{
            list-style: none;
            display: flex;
            align-items: center;
            li{
               margin-left: 15px;
               color: $accent-color;
               font-size: 18px;
            }
        }
    }
    .book-list{
        margin: 0;
        padding: 0;
        counter-reset: list;
        column-count: 2;
        &__item{
            position: relative;
            padding-left: 30px;
            counter-increment: list;
            list-style-type: none;
            margin-bottom: 10px;
            font-family: textos;
            font-size: 15px;
            line-height: 24px;
            letter-spacing: 0.7px;
            font-weight: 400;
            &::before{
                content: counter(list) ". ";
            }
            &::after{
                content: '+';
                position: absolute;left: 0;
                font-family: textos;
                font-size: 15px;
                color: #ffffff;
                background-color: #5fc9b6;
                padding: 0px 6px;
            }
            
        }
        &_sec{
            counter-reset:  list 10;
            display: none;
        }
    @media (max-width: 440px){
        column-count: 1;
    }
    }
    .more_btn{
        max-width: 135px;
        padding: 12px 35px;
        border:none; 
        &:focus{
            outline: none;
        }       
    }

}
.books{
    background-color: #ffffff;
    text-align: center;
    &_title{
        display: block;
        width: 100%;
        text-align: center;
        margin-top: 155px;
        margin-bottom: 90px;
    }
    .other-books{
        &_wrap{
            margin: 0;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
        &-block{
            flex-basis: 33,3%;
            max-width: 315px;            
            &__demo{
                margin: 0 auto;
                position: relative;
                width: 270px;
                height: 410px;
                background-image: url(../img/svg/bookLitle.svg);
                background-repeat: no-repeat;
                h2{
                    position: absolute;
                    top: 75px;
                    left: 75px;
                    font-size: 24px;
                    text-transform: uppercase;
                    color: #ffffff;
                    text-align: center;
                    span{
                        display: block;
                        font-size: 12px;
                        font-family: textos;
                        color: $accent-color;
                        text-transform: none;
                        margin-top: 20px;
                        font-weight: 400;
                    }
                }
                p{
                    position: absolute;
                    bottom: 35px;
                    left: 80px;
                    font-size: 12px;
                    font-family: textos;
                    color: $accent-color;
                    text-transform: none;
                    margin-top: 20px;
                    font-weight: 100;
                }
            }
            &__text{
                margin-top: 40px;
                text-align: center;
                h3{
                    margin: 0;
                    font-size: 24px;
                }
                p{
                    margin: 0;
                    margin-top: 25px;
                    margin-bottom: 25px;
                    font-size: 15px;
                    letter-spacing: 0.7px;
                    font-weight: normal;
                    line-height: 24px;
                }
                a{
                    text-decoration: none;
                    text-transform: uppercase;
                    color: $accent-color;
                    font-family: textos;
                    font-weight: 700;
                }
            }
        }
    }
    &_btn{
        margin: 0 auto;
        margin-top: 90px;
        margin-bottom: 150px;
    }
}
.reviews{
    position: relative;  
    padding-top: 200px;
    padding-bottom: 150px;
    
    @media (max-width: 1020px){
        background-color: $dark-color;
    }
    .block-slider{
        div{
            display: block;
            text-align: center;
        }
        &-ava{
            position: relative;
            margin: 0 auto 27px;
            width: 152px;
            height: 152px;
        }
        &__img{
            margin: 0 auto;            
            width: 152px;
            height: 152px;
            img{
                margin: 0 auto;  
                width: 152px;
                height: 152px;              
            }           
        }
        &-bg{
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            background-image: url(../img/scrumb.png);
            background-repeat: no-repeat;
            width: 48px;
            height: 42px;
        }
        &__title{
            font-size: 24px;
            line-height: 28px;
            letter-spacing: 0.5px;
            color: $accent-color;
        }
        &__subtitle,
        &__text{
            display: block;
            margin: 10px auto;
            max-width: 760px;
            font-family: $textos;
            font-size: 15px;line-height: 24px;
            font-weight: 100;
            letter-spacing: 0.5px;
            color: #696980;
        }
        
    }
    &__svg{
        position: absolute;
        top: 0;
        width: 100%;
        height: 700px;
        background: #42424e;
        clip-path: polygon(0 0, 50% 10%, 100% 0, 100% 90%, 50% 100%, 0 90%);        
    }
}
.slick-dots{
    display: flex;
    justify-content: center;   
    text-align: center;  
    &>li{
        text-align: center; 
        margin: 0 10px;
    }
}
.slick-dots li button{
    text-indent: -9999px;
    text-align: center;    
    width: 8px;
    height: 8px;
    background-color: $accent-color;
    border-radius: 100%;
    margin: 0 10px;
}
.slick-dots li.slick-active button{
    width: 37px;
    border-radius: 10px;    
}

.events{
    padding-top: 150px;padding-bottom: 150px;
}
.events-ava{
    &--svg{
        left:0;
    }
    &--img{
        margin-top: 50px;
        margin-left: 40px;
    }
}
.events-blocks{
    margin-top: 30px;
    &__date{
        font-family: textos;
        font-size: 15px;
        color: #696980;
        letter-spacing: 0.5px;
        line-height: 30px;
    }
    &__title{
        font-size: 18px;
        font-weight: 700;
        color: #44444f;
        letter-spacing: 0.5px;
        line-height: 30px;
    }
    &__text{
        font-size: 15px;
        color: #696980;
        letter-spacing: 0.5px;
        line-height: 24px;
    }
    &--btn{
        max-width: 137px;
        margin-top: 30px;
    }
}
.events--name-top{
    margin-top: 0;
}
.blog{
    background-color: #f5f6fa;
    padding-top: 160px;
    padding-bottom: 160px;
    &--title{
        text-align: center;
        margin-bottom: 80px;
    }
    &-wrap{        
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &-block{
        flex-basis: 33,3%;
    }
    &-img{
        width: 369px;
        height: 224px;
        overflow: hidden;
        position: relative;
        margin-bottom: 20px;
        @media (max-width: 440px){
            width: 300px;
            margin: 10px auto;
        }
        img{
            width: 369px;
            height: 224px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
        @media (max-width: 440px){
                width: 300px;
            }
        }
    }
    &__title{
        font-size: 18px;
        line-height: 24px;
        letter-spacing: 0.7px;
    }
    &__link{
        display: inline-block;
        font-family: textos;
        font-size: 15px;
        letter-spacing: 0.5px;
        line-height: 30px;
        font-weight: 100;
        a{
            text-decoration: none;
            color: $accent-color;
            margin: 0 5px;
        }
        span{
            color: #696980;
        }
    }
    &__text{
        max-width: 370px;
        font-family: textos;
        font-weight: 100;
        font-size: 15px;
        color: #696980;
        line-height: 24px;
        letter-spacing: 0.6px;
    }
    &--btn{
        display: block;
        margin: 85px auto 0 ;
    }
}
.newsletter{
    padding-top: 155px;
    padding-bottom: 155px;
    @media (max-width:700px){
        padding-top: 120px;
        padding-bottom: 120px;
    }
    .block-letter{
        display: flex;
        justify-content: space-between;
        @media (max-width:700px){
            flex-direction: column;
        }
        &-item{
            flex-basis: 45%;
            @media (max-width:700px){
                flex-basis: 100%;
            }
        }
        &__title{
            font-size: 48px;
            line-height: 40px;
            letter-spacing: 0.7px;
            color: $dark-color;
        }
        &__text{
            font-family: $textos;
            font-size: 15px;
            line-height: 24px;
            letter-spacing: 0.75px;
        }
        &__input{
            display: block;
            box-sizing: border-box;
            width: 100%;
            padding: 15px 20px;
            background-color: $dark-color;
            border-radius: 3px;
            border:none;
            color:#fff;            
            &:focus{
                outline: none;
            }
        }
        &__btn{
            margin-top: 20px;
            box-sizing: border-box;
            max-width: none;
            width: 100%;
            cursor: pointer;
        }
    }
}
.footer{
    background-color: $dark-color;
    padding-top: 160px;
    padding-bottom: 160px;
    @media (max-width:700px){
        padding-top: 120px;
        padding-bottom: 120px;
    }
    .block-footer{
        display: flex;
        justify-content: space-between;
        @media (max-width:700px){
            flex-direction: column;
            justify-content: center;
        }
        &__logo{
            display: block;
            width: 140px;
            height: 30px;
            margin-bottom: 30px;
        }
        &__text,
        &__copy{
            margin-top: 0;
            max-width: 370px;
            font-family: textos;
            font-size: 15px;
            line-height: 24px;
            letter-spacing: 0.5px;
            color: #9191b5;
        }
        &__title{            
            font-size: 18px;
            font-weight: 700;
            text-transform: uppercase;
            color: $accent-color;
            margin-bottom: 30px;
        }
        &__list{
            font-family: textos;
            font-size: 15px;
            line-height: 28px;
            letter-spacing: 0.5px;
            color: #9191b5;
            list-style-type: none;
        }
        &__social{
            margin-top: 15px;
            list-style-type: none;
            display: flex;
            li{
                margin-right: 15px;
                a{
                    color: $accent-color;
                }
            }
        }
    }
}

@media (max-width: 1140px){
    .blog{
        &-wrap{
            justify-content: space-around;
        }
        &-block{
            margin-top: 15px;
        }
    }
}
@media (max-width: 980px){
    .books{
        .other-books_wrap{
            justify-content: space-around;
        }
        .other-books-block{
            margin-top: 20px;
        }
    }
}
@media (max-width: 700px){
    .text--secondary{
        margin-top: 50px;
    }
}